import React, { useState } from "react";
import "./Navbar.scss";
import imgfacebook from "../../assits/facebook.png";
import imginsta from "../../assits//instagram.png";
import imgtiktok from "../../assits/tiktok.png";
import imgyoutube from "../../assits/youtube.png";
import img2 from "../../assits/Perfogram_Logo_TransparentBG.png";
function Navbar() {
  let [toggle, setToggle] = useState(false);
  let [value, setValue] = useState("Live");
  let listOfValue = [
    { value: "Live", id: 1 },
    { value: "Record", id: 2 },
  ];
  return (
    <div className="mian-nav d-none d-sm-block">
      <div className="header-inner">
        <div className="img-list">
          <div className="img-box">
            <img src={img2} alt="..." />
          </div>
          <div className="list-icons">
            <div className="img-icons">
              <img src={imgfacebook} alt="..." />
            </div>
            <div className="img-icons">
              <img src={imginsta} alt="..." />
            </div>
            <div className="img-icons">
              <img src={imgyoutube} alt="..." />
            </div>
            <div className="img-icons">
              <img src={imgtiktok} alt="..." />
            </div>
            <div className="img-icons">
              <img src={imgfacebook} alt="..." />
            </div>
            <div className="img-icons">
              <img src={imgtiktok} alt="..." />
            </div>
            <div className="img-icons">
              <img src={imginsta} alt="..." />
            </div>
          </div>
        </div>
        <div className="details">
          <div className="title">
            <div className="box-details">
              <i className="fa-solid fa-users" />
              <span>1.2k</span>
            </div>
            <div className="box-details">
              <i class="fa-regular fa-clock"></i>

              <span>18:23</span>
            </div>
            <div className="box-state">
              <span className="ball"></span>
              <span>{value}</span>
            </div>
          </div>
          <div className="select-box" onClick={() => setToggle(!toggle)}>
            <span>{value}</span>
            <i class="fa-solid fa-chevron-down"></i>
            {toggle && (
              <div className="box-menu">
                {listOfValue &&
                  listOfValue.map((e) => (
                    <span
                      key={e.id}
                      onClick={(item) => {
                        item.preventDefault();
                        item.stopPropagation();
                        setValue(e.value);
                        setToggle(false);
                      }}
                    >
                      {e.value}
                    </span>
                  ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Navbar;
